html[data-theme='light'] {
  --bg-color: #FFF;
  --primary-color: hsl(0, 0%, 93%);
  --secondary-color: hsl(0, 0%, 96%);
  --font-color: #000;
  --border-color: #000;
  --highlight-color: lightblue;

  --lootcrates-color: hsl(0, 0%, 65%);

  --boss-color: hsl(45, 100%, 45%);

  background: lightgray;
  color: var(--font-color);
}

:root {
  --bg-color: #000;
  --primary-color: hsl(0, 0%, 5%);
  --secondary-color: hsl(0, 0%, 10%);
  --font-color: #FFF;
  --border-color: #ddd;
  --highlight-color: darkblue;

  --lootcrates-color: hsl(0, 0%, 70%);

  --boss-color: hsl(45, 100%, 50%);
  background: #333;
  color: var(--font-color);
}

html.color-theme-in-transition,
html.color-theme-in-transition *,
html.color-theme-in-transition *:before,
html.color-theme-in-transition *:after {
  transition: all 700ms !important;
  transition-delay: 0 !important;
}

.feedEntry {
  list-style-type: none;
  margin: .15em 0;
  background: var(--secondary-color);
  padding: .3em .2em;
}

.feedCluster {
  list-style-type: none;
  padding: 0;
  margin-bottom: 0;
}

.card {
  width: 100vw;
  height: 100vh;
  border-width: 0 1px;
  border-style: solid;
  border-color: var(--border-color);
  overflow-y: scroll;
  white-space: normal;
  display: inline-block;
  background: var(--bg-color);
}

@media (min-width: 30em) {
  .card {
    width: 22em;
    margin: 0 1px 0 1px;
    padding: .7em;
  }
}

.card button {
}

button {
  border-radius: 25px;
  cursor: pointer;
  border: 1px solid var(--font-color);
  font-family: "Open Sans","Helvetica Neue",sans-serif;
  font-weight: 700;
  background: var(--bg-color);
  color: var(--font-color);
  margin: .2em;
  padding: .2em .5em;
  vertical-align: middle;
}

button:hover {
  color: var(--bg-color);
  background: var(--font-color);
}

input[type=checkbox] {
  cursor: pointer;
}

.card h1 {
  margin-top: 0;
  font-family: "Lora";
  color: var(--font-color);
}

.cardContainer {
  height: 100vh;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

.entry {
  display: inline-grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: .3em;
  align-items: center;

  animation: fadeIn 1s;
  -webkit-animation: fadeIn 1s;
  -moz-animation: fadeIn 1s;
  -o-animation: fadeIn 1s;
  -ms-animation: fadeIn 1s;
  color: var(--font-color);
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

.entrySeason {
  font-weight: 200;
  font-size: .7em;
}

.entryDay {
  font-weight: 200;
  font-size: .7em;
}

.entryText {
  font-family: "Lora";
}

.feedList {
  padding: 0;
  background: var(--primary-color);
  margin: .4em 0;
  padding: .1em;
}

.typeCheckbox {
  font-size: small;
}

.typeModalColumns {
  columns: 3 20em;
}

.typeModal {
  background: var(--bg-color);
  position: absolute;
  inset: 40px;
  border: 1px solid var(--border-color);
  overflow: scroll;
  border-radius: 4px;
  padding: 20px;
}

.categorySelect {
  columns: 2;
}

.categoryOption input {
  margin-left: 0;
}

.selectedTypeCrumb {
  font-size: x-small;
  max-width: 10em;
  overflow: hidden;
  white-space: nowrap;
  margin: 2px;
  background: var(--highlight-color);
  font-style: oblique;
  border-radius: 5px;
}

.selectedTypes {
  display: flex;
  flex-wrap: wrap;
}

.titleEdit {
  font-size: x-large;
  background-color: var(--bg-color);
  border: 1px solid var(--font-color);
  border-radius: 4px;
  color: var(--font-color);
}

.editButtons {
}

.floatRight {
  float: right;
}

.editButton {
  float: right;
}

.editPanel {
}

.editPanel > * {
  margin: 4px 0;
}

.editPanel > label {
  font-size: small;
}

.spinClark {
  width: 2em;
  height: 2em;
  /*background-image: url("./ballclark.png");*/
  animation: spinny 3s linear infinite;
  -webkit-animation: spinny 3s linear infinite;
  -moz-animation: spinny 3s linear infinite;
  -o-animation: spinny 3s linear infinite;
  -ms-animation: spinny 3s linear infinite;
}

.spinClark>img {
  width: 100%;
  height: 100%;
  animation: fadeIn 1s;
  -webkit-animation: fadeIn 1s;
  -moz-animation: fadeIn 1s;
  -o-animation: fadeIn 1s;
  -ms-animation: fadeIn 1s;

}

@keyframes spinny {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.monitor {
  color: #5988FF;
  text-shadow: 0 0 20px;
  font-weight: 700;
}

.reader {
  color: #A16DC3;
  text-shadow: 0 0 10px;
}

.boss {
  color: var(--boss-color);
  font-weight: 700;
}

.lootcrates {
  font-style: italic;
  color: var(--lootcrates-color);
  font-weight: 700;
}

.bigdeal {
  font-weight: 700;
  color: var(--font-color);
}

.loadMore {
  margin-bottom: 1em;
}

.defaultText {
  color: var(--font-color);
  font-family: "Open sans", helvetica, arial, "sans serif";
}

.editDropdown__control {
  cursor: pointer;
  color: var(--font-color);
  background-color: var(--bg-color)!important;
}

.editDropdown__menu {
  color: var(--font-color);
  background-color: var(--bg-color)!important;
  border: 1px solid var(--border-color);
}

.editDropdown__option:hover, .editDropdown__option--is-focused {
  cursor: pointer;
  background-color: var(--highlight-color)!important;
}
